document.addEventListener('DOMContentLoaded', function() {

   const burgerMenu = document.querySelector('.header__block-burger')
   const menu = document.querySelector('.header__container') 
   const burgerLine = document.querySelectorAll('.header__line')
   const menuM = document.querySelector('.header__ul')

   burgerMenu.addEventListener('click', function () {
      menu.classList.toggle('header__container_active')
      burgerLine.forEach(function(lineBurger){
         lineBurger.classList.toggle('header__line_animation')
      })

      if(menu.classList.contains('header__container_active')) {
         menuM.style.display = 'flex';
         menuM.style.opacity = '1';
      } else {
         menuM.style.opacity = '0';
         menuM.style.display = 'none';
      }
   })

   const anchors = document.querySelectorAll('a[href*="#"]')
   for (let anchor of anchors) {
      anchor.addEventListener('click', function (e) {
         e.preventDefault()
         const blockID = anchor.getAttribute('href').substr(1)
         
         document.getElementById(blockID).scrollIntoView({
            behavior: 'smooth',
            block: 'start'
         })
      })
   }
});